import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Stepper from "react-stepper-horizontal";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
// import { addFaktur, checkReplikasiValidasi, deleteFaktur } from "../FindIndex";
import {
  getValidasiBarangKirimMasak,
  getValidasiBarangMutasiBarang,
  getValidasiBarangMutasiLK,
  getValidasiBarangPindahBarang,
  getValidasiBarangTerimaMasak,
} from "../actions/validasiBarangAction";
import { getDataSelectedValidasiSupplier } from "../actions/validasiSupplierAction";
import Axios from "axios";
import { berhasil, gagal } from "../Alert";
import uuid from "react-uuid";
import HeaderCard from "../HeaderCard";
import { AxiosMasterPut } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const maptostate = (state) => {
  return {
    listValidasiBarang: state.provinsi.listValidasiBarang,
    listDataSelectedValidasiSupplier:
      state.provinsi.listDataSelectedValidasiSupplier,
  };
};

class validasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "no_trx",
          text: "Nomor Transaksi",
        },
        {
          dataField: "total_bruto",
          text: "Total Bruto(Gr)",
        },
        {
          dataField: "total_netto",
          text: "Total Netto(Gr)",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
      ],
      data: [
        {
          tanggal: "23/11/2020",
          noBon: "TESTING-123",
          noFaktur: "FAKTUR-123",
        },
      ],
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      angka: 0,
    };
  }

  componentDidMount() {
    localStorage.setItem("FakturTerpilih", "[]");
    localStorage.setItem("FakturTerpilih_kirim", "[]");
  }

  updateButton(data) {
    this.setState({
      angka: this.state.angka + 1,
    });
    switch (data) {
      case "Pindah Barang":
        this.props.dispatch(getValidasiBarangPindahBarang());
        break;
      case "MuatasiBarang":
        this.props.dispatch(getValidasiBarangMutasiBarang());
        break;
      case "MutasiJenisLK":
        this.props.dispatch(getValidasiBarangMutasiLK());
        break;
      case "Kirim Masak":
        this.props.dispatch(getValidasiBarangKirimMasak());
        break;
      case "Terima Masak":
        this.props.dispatch(getValidasiBarangTerimaMasak());
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  disabledButton = (e) => {
    e.target.disabled = true;
  };
  changeTransaksi = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
    switch (e.target.value) {
      case "Pindah Barang":
        this.props.dispatch(getValidasiBarangPindahBarang());
        break;
      case "MuatasiBarang":
        this.props.dispatch(getValidasiBarangMutasiBarang());
        break;
      case "MutasiJenisLK":
        this.props.dispatch(getValidasiBarangMutasiLK());
        break;
      case "Kirim Masak":
        this.props.dispatch(getValidasiBarangKirimMasak());
        break;
      case "Terima Masak":
        this.props.dispatch(getValidasiBarangTerimaMasak());
        break;
      default:
        console.log("ERROR");
        break;
    }
  };
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getDataSelectedValidasiSupplier());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };

  kirimvalidasi(data) {
    console.log("BERHASIL");
    var hasil = {
      trx_stock: JSON.parse(localStorage.getItem("FakturTerpilih_kirim")),
    };
    switch (data) {
      case "Kirim Masak":
        AxiosMasterPut("validate-stock/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiBarangKirimMasak()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "Terima Masak":
        Axios.put(serverNonMaster + "item/dc/terimamasak", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiBarangTerimaMasak()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "Pindah Barang":
        AxiosMasterPut("validate-stock/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiBarangPindahBarang()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "MuatasiBarang":
        AxiosMasterPut("validate-stock/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiBarangKirimMasak()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "MutasiJenisLK":
        Axios.put(serverNonMaster + "item/dc/mutasilk", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiBarangMutasiLK()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      default:
        break;
    }
  }
  render() {
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      onSelect: (row, isSelect, rowIndex, e) => {
        console.log(row.noFaktur);
        let array = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
        let array2 =
          JSON.parse(localStorage.getItem("FakturTerpilih_kirim")) || [];
        const data = {
          tanggal: row.tanggal,
          no_bon: row.no_bon,
          total_bruto: row.total_bruto,
          total_netto: row.total_netto,
          keterangan: row.keterangan,
          no_trx: row.no_trx,
        };
        const kirim = {
          no_trx: row.no_trx,
          keterangan: row.keterangan,
        };
        if (isSelect) {
          var index1 = array.findIndex((item, i) => {
            return item.no_trx === row.no_trx;
          });
          var index2 = array2.findIndex((item, i) => {
            return item.no_trx === row.no_trx;
          });
          if (index1 < 0) {
            array.push(data);
            array2.push(kirim);
          } else {
            array.splice(index1, 1);
            array2.splice(index2, 1);
          }
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("FakturTerpilih_kirim", JSON.stringify(array2));
        } else {
          var index = array.findIndex((item, i) => {
            return item.noFaktur === row.noFaktur;
          });
          var index3 = array.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          array.splice(index, 1);
          array2.splice(index3, 1);
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("FakturTerpilih_kirim", JSON.stringify(array));
        }
      },
      onSelectAll: (isSelect, rows, e) => {
        var array = [];
        var array2 = [];
        rows.forEach(function (list) {
          var data = {
            tanggal: list.tanggal,
            no_bon: list.no_bon,
            total_bruto: list.total_bruto,
            total_netto: list.total_netto,
            keterangan: list.keterangan,
            no_trx: list.no_trx,
          };
          const kirim = {
            no_trx: list.no_trx,
            keterangan: list.keterangan,
          };
          array.push(data);
          array2.push(kirim);
        });
        if (isSelect) {
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("FakturTerpilih_kirim", JSON.stringify(array2));
        } else {
          localStorage.removeItem("FakturTerpilih");
          localStorage.removeItem("FakturTerpilih_kirim");
        }
      },
    };
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Validasi Barang" />
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Validasi Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-3 col-md-3">
                      <label htmlFor="">Silahkan Pilih Transaksi</label>
                      <Field
                        name="tipeTransaksi"
                        label="Dari"
                        component="select"
                        className="form-control"
                        onChange={this.changeTransaksi("tipeTransaksi")}
                      >
                        <option value="DEFAULT" key="DEFAULT">
                          SILAHKAN PILIH
                        </option>
                        <option value="Pindah Barang" key="Pindah Barang">
                          Pindah Barang
                        </option>
                        <option value="MuatasiBarang" key="Muatasi Barang">
                          Mutasi Barang
                        </option>
                        <option value="MutasiJenisLK" key="Mutasi Jenis LK">
                          Mutasi Jenis LK
                        </option>
                        <option value="Kirim Masak" key="Kirim Masak">
                          Kirim Masak
                        </option>
                        <option value="Terima Masak" key="Terima Masak">
                          Terima Masak
                        </option>
                      </Field>
                    </div>
                    {/* <div className="col-lg-3 col-md-3">
                      <label htmlFor="">Dari</label>
                      <Field
                        name="accFrom"
                        label="Dari"
                        component="select"
                        className="form-control"
                      >
                        <option value="DEFAULT" key="DEFAULT">
                          SILAHKAN PILIH
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-3 col-md-3">
                      <label htmlFor="">Ke</label>
                      <Field
                        name="accTo"
                        label="Ke"
                        component="select"
                        className="form-control"
                      >
                        <option value="DEFAULT" key="DEFAULT">
                          SILAHKAN PILIH
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-3 col-md-3">
                      <Field
                        name="tanggal"
                        label="Tanggal Transaksi"
                        component={renderField}
                        type="date"
                      />
                    </div> */}
                    {/* <div className="col-lg-12">
                      <div className="text-right">
                        <button className="btn btn-primary">
                          <i className="fa fa-eye"></i> Lihat Data
                        </button>
                      </div>
                    </div> */}
                    <div className="col-lg-12 mt-3">
                      <GlobalTabel
                        keyField="no_bon"
                        data={this.props.listValidasiBarang}
                        columns={this.state.columns}
                        search
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                        selectRow={selectRow}
                      />
                      {/* {this.props.listValidasiBarang ? (
                        <ToolkitProvider
                          keyField="no_bon"
                          data={this.props.listValidasiBarang}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                                selectRow={selectRow}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="noFaktur"
                          data={[]}
                          columns={this.state.columns}
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )} */}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next
                          <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12 mt-3">
                      {this.props.listDataSelectedValidasiSupplier ? (
                        <ToolkitProvider
                          keyField={uuid()}
                          data={this.props.listDataSelectedValidasiSupplier}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="noFaktur"
                          data={[]}
                          columns={this.state.columns}
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="row">
                        <div className=" col-lg-6 text-left">
                          <button
                            className="btn btn-primary"
                            onClick={() => this.prevStep()}
                          >
                            <i className="fa fa-chevron-left mr-3"></i>
                            BACK
                          </button>
                        </div>
                        <div className=" col-lg-6 text-right">
                          <button
                            className="btn btn-primary"
                            onClick={() =>
                              this.kirimvalidasi(this.state.tipeTransaksi)
                            }
                          >
                            <i className="fa fa-check mr-3"></i>
                            VALIDASI
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

validasiBarang = reduxForm({
  form: "validasiBarang",
  enableReinitialize: true,
})(validasiBarang);
export default connect(maptostate, null)(validasiBarang);
